import React from "react";
import { Select } from "antd";
import { useQuery } from "react-query3";

export default function VaultSelectBox(props) {
  const { data, isLoading } = useQuery<string[]>("/variable/vault");
  return (
    <Select {...props} loading={isLoading}>
      {data?.map((vault) => (
        <Select.Option key={vault} value={vault}>{vault}</Select.Option>
      ))}
    </Select>
  );
}
